.p-item {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid #ecaf6a;
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
}

.p-browser {
  height: 15px;
  background-color: #ecaf6a;
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 2;
}

.p-circle {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: white;
}

.p-code {
  height: 20px;
  position: sticky;
  z-index: 2;
  background-color: white;
}

.p-img {
  width: 100%;
  transition: all 10s ease;
}

.p-img:hover {
  transform: translateY(-100%);
}
